/* callback__confirmation */
.callback__confirmation {
  display: flex;
  align-items: end; }

.approval label {
  display: inline-block;
  vertical-align: middle;
  position: relative;
  padding: 3px 0 3px 26px;
  font-size: 14px;
  line-height: 16px;
  max-width: 250px;
  cursor: pointer;
  transition: color 0.6s ease, opacity 0.3s ease;
  opacity: 0.8; }
  .approval label::before {
    content: "";
    position: absolute;
    width: 15px;
    height: 15px;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    border: 1px solid #ffffff;
    border-radius: 2px;
