.callback-modal {
  display: none;
  /* Делаем по центру модальное окно  */
  position: fixed;
  left: 0;
  right: 0;
  bottom: auto;
  top: 146px;
  margin: auto;
  width: 500px;
  z-index: 10;
  padding: 40px;
  border-radius: 3px; }

/* Реализация открытия модального окна */
.callback-modal.show {
  display: block;
  animation: bounce 0.6s; }

.callback-modal .callback-modal__block {
  margin: 0; }

.callback-modal__block span {
  max-width: 380px;
  margin-bottom: 40px; }

/* callback-modal__form */
.callback-modal__form .form__list {
  display: block;
  margin: 0 0 25px; }

.callback-modal__form .callback__item {
  margin-bottom: 20px; }
  .callback-modal__form .callback__item:last-child {
    margin-bottom: 0; }

/* callback-modal__confirmation */
.callback-modal__confirmation {
  display: block; }

.callback-modal__confirmation .callback__button {
  width: 100%;
  margin-right: 0;
  margin-bottom: 20px; }

/* callback-modal__approval */
.callback-modal__approval label {
  max-width: 395px; }
  .callback-modal__approval label:active {
    color: #7b0eff;
    opacity: 1; }

/* callback-modal__btn-close */
.callback-modal__btn-close {
  position: absolute;
  top: 15px;
  right: 15px;
  width: 40px;
  height: 40px;
  padding: 10px;
  border: none;
  background-color: transparent;
  cursor: pointer; }
  .callback-modal__btn-close::before, .callback-modal__btn-close::after {
    content: '';
    position: absolute;
    width: 20px;
    height: 2px;
    background-color: #ffffff;
    top: 20px;
    right: 10px; }
  .callback-modal__btn-close::after {
    transform: rotate(-45deg); }
  .callback-modal__btn-close::before {
    transform: rotate(45deg); }

/* Modal-login Animations */
/* Анимация для открытия модального окна */
@keyframes bounce {
  0% {
    transform: translateY(-2000px); }
  70% {
    transform: translateY(30px); }
  90% {
    transform: translateY(-10px); }
  100% {
    transform: translateY(0); } }

