.social {
  line-height: 0;
  list-style: none;
  margin: 0;
  padding: 0;

  display: flex;
  flex-wrap: wrap;
  max-width: 225px;
  gap: 15px;
}

.footer__social.social {
  margin-left: 11px;
}

.social__link {
  display: inline-block;
  vertical-align: middle;
  width: 40px;
  height: 40px;
  padding: 5px;
}

.social__icon {
  width: 100%;
  height: 100%;
  fill: $color-alice-blue;
  transition: fill $trans-default;

  @include effects-off {
    &:hover,
    &:focus {
      fill: $deep-sky-blue;
    }

    &:active {
      fill: $deep-dodger-blue;
    }
  }

  &:active {
    fill: $deep-dodger-blue;
  }
}
